.container {
    display: flex;
}
.sidebar {
    width: 289px;
    min-height: 100vh;
    background-color: #fff;
    border-right: 1px solid #e8ebee;
    overflow-x: hidden;
    position: fixed;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.content {
    padding-left: 289px;
    width: 100%;
    height: 100vh;
}
.contentBody {
    padding: 108px 40px 40px;
    background-color: var(--bg-light);
    height: 100%;
    overflow-y: auto;
}
.nav {
    background: #fff;
    border-bottom: 1px solid #ececec;
    display: flex;
    width: calc(100% - 289px);
    padding: 0 40px;
    position: fixed;
    z-index: 999;
}
.sidebarMenuItemLink {
    height: 51px;
    line-height: 51px;
    font-size: 18px;
    padding: 0 20px;
    width: 100%;
    display: inline-block;
}
.sidebarMenuItemLink.active {
    background-color: black;
    color: #fff;
}
.initialsInfoLight {
    background-color: #f6f7f9;
    border-radius: 4px;
    cursor: pointer;
    width: 48px;
    min-width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .2s;
}